<template>
  <div id="app" v-if="userInfo.status === 'login'">
    <!-- <div id="app"> -->
    <audio controls hidden ref="invest_sound"></audio>
    <audio controls hidden ref="cost_sound"></audio>
    <el-header style="text-align: right; font-size: 12px">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="6">
          <h3 style="margin: 0;text-align: left;">后台管理</h3>
        </el-col>
        <el-col :span="6" style="float: right">
          <el-switch v-model="is_maintain" @change="setMaintain" active-text="网站维护中" inactive-text="网站正常">
          </el-switch>
        </el-col>
        <el-col :span="6" style="float: right">
          <el-dropdown @command="commands">
            <span class="username">{{ userInfo.data.username }}</span>
            <i class="el-icon-arrow-down" style="margin-right: 15px"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="logout">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
      </el-row>
    </el-header>
    <el-container style="height: 500px">
      <el-aside width="200px">
        <el-menu :router="true" text-color="#ddd" class="menu" :default-active="actives">
          <mainmunu :routers="routers" />
        </el-menu>
      </el-aside>

      <el-container>
        <el-main>
          <router-view />
        </el-main>
      </el-container>
    </el-container>
  </div>
  <login v-else-if="userInfo.status === 'logout'" />
</template>
<script>
import Fetch from './utils/fetch'
import mainmunu from './menu'
import login from './views/login'

export default {
  components: {
    mainmunu,
    login,
  },
  data() {
    return {
      is_maintain: false,
      routers: [],
      openeds: [],
      actives: '',
      pstt: '',
    }
  },
  computed: {
    userInfo() {
      return { ...this.$store.state.user }
    },
  },
  methods: {
    test() {
      //debugger
    },
    FetchInit() {
      this.pstt = false && this.$store.state.user.data.pstt
      Fetch('/api/api/config', {
        headers: {
          'content-type': 'application/json',
          Authorization: `Bearer ` + localStorage.getItem('pstt'),
        },
      })
        .then(r => {
          console.log(r)
          this.is_maintain = r.data.is_maintain
          this.$store.commit('setConfig', r.data)
          return Fetch('/sys/user/info')
        })
        .then(r => this.$store.commit('setLogin', r.data))
        .catch(() => {
          this.$store.commit('setLogout')
          this.$store.commit('cleanConfig')
        })
    },
    commands(command) {
      switch (command) {
        case 'logout':
          this.logout()
          break
      }
    },
    logout() {
      localStorage.removeItem('pstt')
      this.$store.commit('setLogout')
      this.$store.commit('cleanConfig')
      this.FetchInit()
    },
    audio() {
      Fetch('/sys/config/audio').then(res => {
        if (res.data.invest_sound) {
          this.$refs.invest_sound.src = res.data.invest_sound
          this.$refs.invest_sound.play()
        }

        if (res.data.cost_sound) {
          this.$refs.cost_sound.src = res.data.cost_sound
          this.$refs.cost_sound.play()
        }
      })
    },
    setMaintain() {
      Fetch('/sys/config/setmaintain', {
        status: this.is_maintain ? 'Y' : 'N',
      }).then(res => {
        this.is_maintain = res.data.is_maintain
      })
    },
  },
  created() {
    window.addEventListener('beforeunload', () => {
      localStorage.setItem('state', JSON.stringify(this.$store.state))
    })
    this.routers = this.$router.options.routes
    this.actives = this.$router.history.getCurrentLocation()
  },
  mounted() {
    this.test()
    this.FetchInit()
    localStorage.getItem('state') &&
      this.$store.replaceState(Object.assign(this.$store.state, JSON.parse(localStorage.getItem('state'))))
    setTimeout(() => {
      this.audio()
    }, 1000)
    if (localStorage.getItem('pstt') != '') {
      setInterval(() => {
        this.audio()
      }, 10000)
    }
  },
  beforeCreate() {
    localStorage.getItem('state') &&
      this.$store.replaceState(Object.assign(this.$store.state, JSON.parse(localStorage.getItem('state'))))
  },
  beforeMount() {
    localStorage.getItem('state') &&
      this.$store.replaceState(Object.assign(this.$store.state, JSON.parse(localStorage.getItem('state'))))
  },
}
</script>

