import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    config: {},
    user: {
      status: 'init',
      data: null,
    },
  },
  mutations: {
    setLogin(state, data) {
      state.user = {
        status: 'login',
        data: { ...data },
      }
    },
    setLogout(state) {
      state.user = {
        status: 'logout',
        data: null,
      }
    },
    cleanUserData(state) {
      state.user = {
        status: 'init',
        data: null,
      }
    },
    setConfig(state, config) {
      localStorage.setItem('ssid', config.ssid)
      state.config = { ...config }
    },
    cleanConfig(state) {
      // localStorage.removeItem('ssid')
      state.config = {}
    },
  },
  actions: {},
})
