<template>
  <div class="contanter">
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="0"
      class="login"
      @submit.native.prevent="submitForm"
      v-loading="loading"
    >
      <el-form-item prop="username">
        <el-input
          type="text"
          v-model="ruleForm.username"
          placeholder="请输入用户名"
          autocomplete="off"
          prefix-icon="el-icon-user"
        />
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          type="password"
          v-model="ruleForm.password"
          placeholder="请输入密码"
          autocomplete="off"
          prefix-icon="el-icon-lock"
        />
      </el-form-item>
      <!-- <el-form-item prop="password">
        <el-input
          type="password"
          v-model="ruleForm.sf_code"
          placeholder="请输安全码"
          autocomplete="off"
          prefix-icon="el-icon-lock"
        />
      </el-form-item> -->
      <!-- <p v-show="msg_show">{{ msg }}</p> -->
      <!-- <el-form-item>
        <el-button type="primary" @click="dx_msg" style="width: 100%">
          获取短信验证码
        </el-button>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          type="text"
          v-model="ruleForm.vf_code"
          placeholder="请输短信验证码"
          autocomplete="off"
          prefix-icon="el-icon-lock"
        />
      </el-form-item> -->
      <el-form-item>
        <el-button type="primary" native-type="submit" style="width: 100%">
          登陆
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import Fetch from '../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      ruleForm: {
        username: '',
        password: '',
        sf_code: '',
        vf_code: '',
      },
      msg_show: false,
      msg: '',
      rules: {
        username: [{ required: true, message: '请输入用户名' }],
        password: [{ required: true, message: '请输入密码' }],
      },
    }
  },
  methods: {
    dx_msg() {
      Fetch('/api/api/sendsms', {
        sf_code: this.ruleForm.sf_code,
      }).then(r => {
        console.log(r)
        this.msg = r.data.msg
        this.msg_show = true
        this.sf_code = r.data.sf_code
      })
    },
    submitForm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.loading = true
          Fetch('/api/api/login', { ...this.ruleForm })
            .then(r => {
              if (typeof r.pstt == 'string') {
                localStorage.setItem('pstt', r.pstt)
              }
              this.$store.commit('setLogin', r)
              this.loading = false
            })
            .catch(() => (this.loading = false))
        }
      })
    },
  },
}
</script>
<style lang="less" scoped>
.contanter {
  display: flex;
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  height: 100%;
}
.login {
  width: 300px;
  padding: 50px;
  margin: auto;
  border: 1px solid #efefef;
  border-radius: 10px;
  box-shadow: 0 0 20px #efefef;
}
</style>
